const mapStateToProps = (state) => {
  return {
    selectmarketplacetype: state.Dataoperation.selectmarketplacetype,
  }
}
const mapDispatchToProps = (dispatch) => {
  return {
    selectMarketplacetype:(s)=>{
      dispatch(selectMarketplacetype(s))
    }
   
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(CreateDataDashboardPage)

